@media only screen and (min-device-width: 768px)  {


	.horizontal-scroll {
		 -webkit-animation: backgroundScroll 20s cubic-bezier(.52,.13,.48,.98)  infinite;
		animation: backgroundScroll 20s cubic-bezier(.52,.13,.48,.98)  infinite;
	}

}
